import s from "./Dialogs.module.css";
import DialogsItem from './DialogsItem/DialogsItem'
import Message from './Message/Message'
import SendMessage from "./SendMessage/SendMessage";
import React from "react";


const Dialogs = (props) => {
    let messagesElements = props.dialogsPage.messagesData.map((m) => (<Message message={m.message}/>))
    let dialogsElements = props.dialogsPage.dialogsData.map((d) => (<DialogsItem name={d.name} id={d.id}/>))

    return (
        <div className={s.dialogs}>
            <div className={s.dialogs_item}>
                {dialogsElements}
            </div>
            <div className={s.messages}>
                {messagesElements}
                <SendMessage sendMessage={props.sendMessage}/>
            </div>
        </div>
    )
}

export default Dialogs;