import React from "react";
import s from "./Users.module.css";
import {NavLink} from "react-router-dom";
import Paginator from "../common/Paginator/Paginator";


const Users = (props) => {

    return <div>
        <Paginator totalCount={props.totalCount} pageSize={props.pageSize} currentPage={props.currentPage}
                   onPageChanged={props.onPageChanged}/>
        {
            props.users.map(u => <div key={u.id}>
                <div className={s.user}>
                    <div className={s.img_block}>
                        <NavLink to={"/profile/" + u.id}>
                            <img className={s.userPhoto}
                                 src={u.photos.small != null ? u.photos.small : "https://vk-wiki.ru/wp-content/uploads/2019/06/user-1.png"}
                                 alt=""/>
                        </NavLink>
                        <div>
                            {u.followed ?
                                <button disabled={props.followingProgress.some(id => id === u.id)}
                                        onClick={() => props.unfollowThunk(u.id)}>UNFOLLOW</button> :
                                <button disabled={props.followingProgress.some(id => id === u.id)}
                                        onClick={() => props.followThunk(u.id)}>FOLLOW</button>}
                        </div>
                    </div>
                    <div className={s.user_info}>
                        <div>
                            <div>{u.name}</div>
                            <div>{u.status}</div>
                        </div>
                        <div>
                            <div>{"u.location.country"}</div>
                            <div>{"u.location.city"}</div>
                        </div>
                    </div>
                </div>
            </div>)
        }
    </div>
}
export default Users;