import React from 'react'

class Status extends React.Component {
    state = {
        editMode: false,
        statusText: this.props.status
    }
    activateEditMode = () => {
        this.setState({
            editMode: true
        })
    }
    deactivateEditMode = () => {
        this.setState({
            editMode: false
        })
        this.props.updateStatus(this.state.statusText)

    }
    onChangeStatusText = (e) => {
        this.setState({
            statusText: e.target.value
        })
    }

    render() {
        return (
            <div>
                <h2>STATUS</h2>
                {this.state.editMode ?
                    <div><input onChange={this.onChangeStatusText} autoFocus={true}
                                onBlur={this.deactivateEditMode} value={this.state.statusText}/>
                    </div>
                    :
                    <div><span onDoubleClick={this.activateEditMode}>{this.props.status ||"write something"}</span></div>}
            </div>
        )
    }

}


export default Status;

