import s from "./Nav.module.css"
import {NavLink} from "react-router-dom";
import {useSelector} from "react-redux";


const Nav = (props) => {
    let authUserId = useSelector(state => state.auth.id)
    return (
        <div className={s.nav}>
            <nav className={s.nav_links}>
                <div className={s.nav_link}><NavLink to={"/profile/" + authUserId}
                                                     className={s.nav_item}
                                                     activeClassName={s.active}>Profile</NavLink></div>
                <div className={s.nav_link}><NavLink to='/music' className={s.nav_item}
                                                     activeClassName={s.active}>Music</NavLink></div>
                <div className={s.nav_link}><NavLink to="/news" className={s.nav_item}
                                                     activeClassName={s.active}>News</NavLink></div>
                <div className={s.nav_link}><NavLink to="/dialogs" className={s.nav_item}
                                                     activeClassName={s.active}>Messages</NavLink></div>
                <div className={s.nav_link}><NavLink to='/settings' className={s.nav_item}
                                                     activeClassName={s.active}>Settings</NavLink></div>
                <div className={s.nav_link}><NavLink to='/users' className={s.nav_item}
                                                     activeClassName={s.active}>Users</NavLink></div>
            </nav>
            <div className={s.friends}>
                <h2>Friends</h2>
                {props.navPage.friendsData.map((f) =>
                    <div className={s.friends_items} key={f.name}>
                        <div className={s.avatar}></div>
                        <div className={s.friend}>{f.name}</div>
                    </div>
                )
                }

            </div>
        </div>
    )
}

export default Nav;


