import ProfileInfo from "./User/ProfileInfo"
import React from 'react'
import PostsContainer from "./Posts/PostsContainer";
import {connect, useSelector} from "react-redux";
import {
    getUserThunk,
    savePhoto,
    setIsOwner,
    setIsOwnerThunk,
    unSetIsOwnerThunk
} from "../../Redux/reducers/userProfileReducer";
import {withRouter} from "react-router";
import {compose} from "redux";
import {getStatusThunk} from "../../Redux/reducers/statusReducer";
import {getProfile, getStatus, getUserId} from "../../Redux/Selectors/Selectors";


class ProfileContainer extends React.Component {
    refreshProfile() {

        let userId = this.props.match.params.userId

        if (this.props.authUserId == userId) {
            this.props.setIsOwnerThunk();
        } else {
            this.props.unSetIsOwnerThunk()
        }
        if (!userId) {
            this.props.history.push("/login")
        }
        this.props.getUserThunk(userId)
        this.props.getStatusThunk(userId)
    }

    componentDidMount() {
        this.refreshProfile()
    }

    componentDidUpdate(prevProps, prevState) {
        if (prevProps.status !== this.props.status)
            this.setState({status: this.props.status})
        if (prevProps.match.params.userId !== this.props.match.params.userId) {
            this.refreshProfile()
        }
    }

    render() {
        return (
            <div>
                <ProfileInfo savePhoto={this.props.savePhoto} profile={this.props.profile} {...this.props.profile}  />
                <PostsContainer/>
            </div>
        )
    }
}

let mapStateToProps = (state) => {
    return {
        profile: getProfile(state),
        status: getStatus(state),
        authUserId: getUserId(state),
    }
}

export default compose(connect(mapStateToProps, {
    getUserThunk,
    getStatusThunk,
    setIsOwnerThunk,
    unSetIsOwnerThunk,
    savePhoto
})/*,
    withAuthRedirect*/, withRouter)(ProfileContainer);

