import React from 'react'
import {Field, reduxForm} from 'redux-form'
import {FormControl} from "../components/common/FormControl/FormControl";
import {login, minLengthCreator, required} from "../Utilits/Validatots/Valodators";
import {Redirect} from "react-router";
import s from '../components/common/FormControl/FormControl.module.css'

const minLength6 = minLengthCreator(6);
const InPut = FormControl("input");

const Login = (props) => {
    const onSubmit = (formData) => {

        let email = formData.login;
        let password = formData.password;
        let rememberMe = formData.checkbox;
        props.loginThunk(email, password, rememberMe)
    }

    if (props.isAuth) {
        return <Redirect to={'/profile/' + props.id}/>
    }

    return (
        <div>
            <h2>Login</h2>
            <LoginReduxForm onSubmit={onSubmit}/>
        </div>
    )

}

const LoginForm = (props) => {
    return (
        <form onSubmit={props.handleSubmit}>
            <div><Field placeholder={"Login"} validate={[required, login]} name={"login"} component={InPut}/>
            </div>
            <div><Field placeholder={"Password"} validate={[required, minLength6]} name={"password"}
                        component={InPut}/></div>
            <div><Field name={"checkbox"} component={"input"} type={"checkbox"}/> Remember me</div>
            {props.error && <div className={s.formError}>
                {props.error}
            </div>}
            <div>
                <button type={"submit"}>Submit</button>
            </div>
        </form>
    )

}
let LoginReduxForm = reduxForm({form: 'login'})(LoginForm)
export default Login;