import {authAPI, userAPI} from "../../API/Api";
import {followUnfollowHelper} from "../../Utilits/Helper";

const FOLLOW = 'FOLLOW';
const UNFOLLOW = 'UNFOLLOW';
const SET_USERS = 'SET_USERS';
const SET_CURRENT_PAGE = 'SET_CURRENT_PAGE';
const SET_TOTAL_USERS_COUNT = 'SET_TOTAL_USERS_COUNT';
const PRELOADER = 'PRELOADER';
const TOGGLE_FOLLOWING_PROGRESS = 'TOGGLE_FOLLOWING_PROGRESS';


let initialState = {
    users: [],
    totalCount: 60,
    pageSize: 10,
    currentPage: 1,
    isFetching: false,
    followingProgress: []


};
window.state = initialState;

const usersReducer = (state = initialState, action) => {
    switch (action.type) {
        case FOLLOW:
            return {
                ...state,
                users: followUnfollowHelper(state.users, action.userId, {followed: true}, "id")
            }
        case UNFOLLOW:
            return {
                ...state,
                users: followUnfollowHelper(state.users, action.userId, {followed: false}, "id")
            }
        case SET_USERS: {
            return {...state, users: [...action.users]}
        }
        case SET_CURRENT_PAGE: {
            return {...state, currentPage: action.currentPage}
        }
        case SET_TOTAL_USERS_COUNT: {
            return {...state, totalCount: action.totalCount}
        }
        case PRELOADER: {
            return {...state, isFetching: action.isFetching}
        }
        case TOGGLE_FOLLOWING_PROGRESS: {
            return {
                ...state,
                followingProgress: action.isProgress ? [...state.followingProgress, action.id]
                    : state.followingProgress.filter(id => id !== action.id)
            }
        }

        default:
            return state;
    }
}

export const followSuccess = (userId) => ({type: FOLLOW, userId})
export const unfollowSuccess = (userId) => ({type: UNFOLLOW, userId})
export const setUsers = (users) => ({type: SET_USERS, users})
export const setCurrentPage = (currentPage) => ({type: SET_CURRENT_PAGE, currentPage})
export const setTotalUsersCount = (totalCount) => ({type: SET_TOTAL_USERS_COUNT, totalCount})
export const preloader = (isFetching) => ({type: PRELOADER, isFetching})
export const toggleFollowingProgress = (isProgress, id) => ({type: TOGGLE_FOLLOWING_PROGRESS, isProgress, id})


//////////////////////////////////////////////////////////////////////////////////////////////////////////
export const getUsersThunk = (currentPage, pageSize) => {
    return async (dispatch) => {
        dispatch(setCurrentPage(currentPage))
        dispatch(preloader(true))
        let response = await userAPI.getUsers(currentPage, pageSize)
        dispatch(preloader(false))
        dispatch(setTotalUsersCount(response.data.totalCount))
        dispatch(setUsers(response.data.items, response.data.totalCount));
    }
}

const followUnfollow = (apiMethod, userId, action) => {
    return (
        async (dispatch) => {
            dispatch(toggleFollowingProgress(true, userId)
            )
            let response = await apiMethod(userId)
            if (response.data.resultCode === 0) {
                dispatch(action(userId))
            }
            dispatch(toggleFollowingProgress(false, userId))
        }
    )
}

export const followThunk = (userId) => {
    return followUnfollow(userAPI.follow, userId, followSuccess)
}
export const unfollowThunk = (userId) => {
    return followUnfollow(userAPI.unfollow, userId, unfollowSuccess)
}
//////////////////////////////////////////////////////////////////////////////////////////////////////////
export default usersReducer;
