export const required = (value) => {
    if (!!value) return undefined;
    return "Field if required "
}
export const maxLengthCreator = (maxLenght) => (value) => {
    if (value && value.length > maxLenght) return "Max lenght is " + maxLenght
    return undefined
}
export const minLengthCreator = (minLenght) => (value) => {
    if (value && value.length < minLenght) return "Min lenght is " + minLenght
    return undefined
}
export const login = (value) => {
    if (typeof value == 'string' && value.includes("@")) return undefined
    if (!value.includes("@")) return "Write correct email"
}