import React, {useState, useEffect} from 'react'
import {setStatus} from "../../../../Redux/reducers/statusReducer";

const StatusHook = (props) => {
    let [editMode, setEditMode] = useState(false)
    let [statusText, changeStatusText] = useState(props.status)
    useEffect(() => {
        changeStatusText(props.status)
    }, [props.status])
    let activateEditMode = () => {
        setEditMode(true)
    }
    let deactivateEditMode = () => {
        setEditMode(false)
        props.updateStatus(statusText)

    }
    let onChangeStatusText = (e) => {
        changeStatusText(e.target.value)
    }
    return (
        <div>
            <h2>STATUS</h2>
            {editMode ?
                <div><input onChange={onChangeStatusText} onBlur={deactivateEditMode} value={statusText}
                            autoFocus={true}/></div>
                :
                <div><span onDoubleClick={activateEditMode}>{props.status || "write something"}</span></div>}
        </div>
    )
}


export default StatusHook;

