import s from "./ProfileInfo.module.css"
import Preloader from "../../common/Preloader/Preloader";
import StatusContainer from "./Status/StatusContainer";
import {useSelector} from "react-redux";

const ProfileInfo = (props) => {
    let isOwner = useSelector(state => state.userProfilePage.isOwner)
    if (!props.profile) {
        return <Preloader/>
    }
    const onMainPhotoSelected = (e) => {
        if (e.target.files.length) {
            props.savePhoto(e.target.files[0])
        }
    }
    return (

        <div className={s.user_page}>
            <div className={s.content_img}>
                <img className={s.content_img} src='https://guruturizma.ru/wp-content/uploads/2014/07/Thailand-1.jpg'/>
            </div>
            <div className={s.logo_info}>
                <img className={s.logo}
                     src={props.profile.photos.small || "https://vk-wiki.ru/wp-content/uploads/2019/06/user-1.png"}
                     alt=""/>


                <div className={s.info}>
                    <div className={s.user_name}>
                        {props.profile.fullName}
                    </div>
                    <div className={s.user_info}>
                        Date of Birth:28.11.2000
                        <br/>
                        City:Tver
                        <br/>
                        Education:TvGU
                        <br/>
                        <StatusContainer/>
                        <br/>
                        <h2 style={{margin: 0}}>SOCIAL</h2>
                        WebSite:{props.profile.contacts.website}
                        <br/>
                        Twitter:{props.profile.contacts.twitter}
                    </div>
                </div>
                <div>
                    {isOwner && <input type="file" onChange={onMainPhotoSelected}/>}
                    Looking For A Job:{props.profile.lookingForAJob ?
                    <img src={"https://media.tenor.com/images/7869871eef03e046040956a5a4f738c5/tenor.gif"}/> :
                    <img src={"https://i.gifer.com/2Nrh.gif"}/>}
                </div>
            </div>
        </div>
    )
}

export default ProfileInfo;


