import React from 'react';
import s from "./Post.module.css";
import {Field, reduxForm} from "redux-form";
import {maxLengthCreator, required} from "../../../Utilits/Validatots/Valodators";
import {FormControl} from "../../common/FormControl/FormControl";

const maxLength10 = maxLengthCreator(10);
const TextArea = FormControl("Textarea");
const Posts = (props) => {

    let onSubmit = (postData) => {
        props.addPost(postData.postData)
    }
    return (
        <div>
            <div className={s.newPost}>
                <AddPostReduxForm onSubmit={onSubmit}/>
            </div>
            {
                props.postData.map(p =>
                    <div className={s.postBlock} key={p.id}>
                        <div className={s.post}>
                            <div className={s.avatar}></div>
                            <div className={s.message}>{p.message}</div>
                        </div>
                        <div className={s.likes}>Likes: {p.likesCount}</div>
                    </div>
                )
            }
        </div>
    )
}


const AddPostForm = (props) => {
    return (<form onSubmit={props.handleSubmit}>
            <Field name={'postData'} component={TextArea} placeholder='Write something...'
                   validate={[required, maxLength10]}/>
            <button type={"submit"} className={s.addBtn}>Add</button>
        </form>
    )
}
const AddPostReduxForm = reduxForm({form: 'addPost'})(AddPostForm)
export default Posts;
