const ADD_POST = "ADD-POST";
let initialState = {
    postData: [
        {id: '1', message: "Hey!I use social network", likesCount: 12},
        {id: '2', message: "Hey!It is my second post!", likesCount: 14},
        {id: '3', message: "Hey!It is my first post!", likesCount: 17},
        {id: '4', message: "Hey!", likesCount: 4}
    ]
}
const profileReducer = (state = initialState, action) => {

    switch (action.type) {
        case ADD_POST: {
            return {
                ...state,
                postData: [...state.postData, {id: "5", message: action.postData, likesCount: 0}],
            }
        }
        default: {
            return state;
        }
    }
}

export const addPost = (postData) => {
    return {type: ADD_POST, postData}
}
export default profileReducer;