import React from 'react'
import {Field, reduxForm} from "redux-form";
import {FormControl} from "../../common/FormControl/FormControl";
import {maxLengthCreator, required} from "../../../Utilits/Validatots/Valodators";

const maxLength10 = maxLengthCreator(10)
const TextArea = FormControl("Textarea");

const SendMessage = (props) => {
    let onSubmit = (message) => {
        props.sendMessage(message.message)
    }
    return (
        <SendMessageReduxForm onSubmit={onSubmit}/>
    )
}
////////////////////////////////////////////////////////////////////////////////////////////////////////////////
const sendMessForm = (props) => {
    return (
        <form onSubmit={props.handleSubmit}>
            <Field component={TextArea} validate={[required, maxLength10]} name={"message"}/>
            <button type={"submit"}>Send messages</button>
        </form>
    )
}

///////////////////////////////////////////////////////////////////////////
let SendMessageReduxForm = reduxForm({form: 'sendMessage'})(sendMessForm)
export default SendMessage;
