import {userAPI} from "../../API/Api";

const SET_USER = 'SET_USER';
const SET_IS_OWNER = 'SET_IS_OWNER';
const UNSET_IS_OWNER = 'UNSET_IS_OWNER';
const SAVE_PHOTO = 'SAVE_PHOTO';

let initialState = {
    profile: null,
    isOwner: false
};

const userProfileReducer = (state = initialState, action) => {
    switch (action.type) {
        case SET_USER: {
            return {...state, profile: action.user}
        }
        case SAVE_PHOTO: {
            return {...state, profile: {...state.profile, photos: action.photos}}
        }
        case SET_IS_OWNER: {
            return {...state, isOwner: true}
        }
        case UNSET_IS_OWNER: {
            return {...state, isOwner: false}
        }
        default:
            return state;
    }
}
export const getUserThunk = (userId) => {
    return async (dispatch) => {
        let response = await userAPI.setUser(userId)
        dispatch(setUser(response.data))
    }
}
export const savePhoto = (photoFile) => {
    return async (dispatch) => {
        let response = await userAPI.savePhoto(photoFile)
        dispatch(savePhotoSuccess(response.data.data.photos))
    }
}
export const setIsOwnerThunk = () => {
    return (dispatch) => {
        dispatch(setIsOwner())
    }
}
export const unSetIsOwnerThunk = () => {
    return (dispatch) => {
        dispatch(unSetIsOwner())
    }
}


export const setUser = (user) => ({type: SET_USER, user})
export const savePhotoSuccess = (photos) => ({type: SAVE_PHOTO, photos})
export const setIsOwner = () => ({type: SET_IS_OWNER})
export const unSetIsOwner = () => ({type: UNSET_IS_OWNER})
export default userProfileReducer;