import React from "react";
import s from "./Header.module.css";
import {NavLink} from "react-router-dom";
import {logOutThunk} from "../../Redux/reducers/authReducer";

const Header = (props) => {


    return (
        <header className={s.header}>
            <img
                src='https://c0.klipartz.com/pngpicture/468/798/sticker-png-logo-triangle-brand-euphoria-angle-triangle.png'/>
            <h2>euphoriA</h2>
            {props.isAuth ? <div>{props.login}
                <button onClick={props.logOutThunk}>LogOut</button>
            </div> : <NavLink to={"/login"}>Login</NavLink>}


        </header>
    )
}

export default Header;