import React from 'react'
import Status from "./Status";
import {compose} from "redux";
import {connect} from "react-redux";
import {updateStatusThunk} from "../../../../Redux/reducers/statusReducer";
import {withRouter} from "react-router";
import StatusHook from "./StatusHook";

class StatusContainer extends React.Component {

    render() {
        return (
            <StatusHook statusText={this.props.statusText} status={this.props.status}
                    updateStatus={this.props.updateStatusThunk}/>
        )
    }

}

let mapStateToProps = (state) => {
    return {
        status: state.status.status,
        statusText: state.status.statusText
    }
}

export default compose(withRouter, connect(mapStateToProps, {updateStatusThunk}))(StatusContainer);

